/* eslint-disable */
import Vue from 'vue';
import ProgressVue from './component/Progress.vue'

const ProgressStore = {
    status: null,
    percent: 0,
    folding: false,
    settings: {
        minimum: 0.08,
        speed: 200,
        trickle: true,
        trickleSpeed: 200
    },
    
    set: function (n) {
        let that = this;
        n = clamp(n, that.settings.minimum, 1);
        that.status = (n === 1 ? null : n);
        
        that.percent = toBarPerc(this.status || 0);

        var speed = that.settings.speed;

        queue(function (next) {
            if (n === 1) {
                setTimeout(function () {
                    that.remove();
                    next();
                }, speed);
            } else {
                setTimeout(next, speed);
            }
        });

        return that;
    },
    isStarted: function () {
        return typeof this.status === 'number';
    },
    start: function () {
        let that = this;
        if (!that.status) that.set(0);
        that.set(0);
        var work = function () {
            setTimeout(function () {
                if (!that.status) return;
                that.trickle();
                work();
            }, that.settings.trickleSpeed);
        };

        if (that.settings.trickle) work();

        return this;
    },
    done: function (force) {
        if (!force && !this.status) return this;

        return this.inc(0.3 + 0.5 * Math.random()).set(1);
    },
    inc: function (amount) {
        var n = this.status;

        if (!n) {
            return this.start();
        } else if (n > 1) {
            return;
        } else {
            if (typeof amount !== 'number') {
                if (n >= 0 && n < 0.2) { amount = 0.1; }
                else if (n >= 0.2 && n < 0.5) { amount = 0.04; }
                else if (n >= 0.5 && n < 0.8) { amount = 0.02; }
                else if (n >= 0.8 && n < 0.99) { amount = 0.005; }
                else { amount = 0; }
            }

            n = clamp(n + amount, 0, 0.994);
            //n = clamp(n + amount, 1);
            return this.set(n);
        }
    },
    trickle: function () {
        return this.inc();
    },
    remove: function () {
        this.folding = true;
        return this.folding
    }
};  

function clamp(n, min, max) {
    if (n < min) return min;
    if (n > max) return max;
    return n;
}

function toBarPerc(n) {
    return (n) * 100;
}

let queue = (function () {
    var pending = [];

    function next() {
        var fn = pending.shift();
        if (fn) {
            fn(next);
        }
    }

    return function (fn) {
        pending.push(fn);
        if (pending.length == 1) next();
    };
})();


function initStore(Vue) {
    return new Vue({
        data: () => ({
            ProgressStore: ProgressStore
        }),
        methods: {
            start: function() {
                this.ProgressStore.start();
            },
            set: function (n) {
                this.ProgressStore.set(n);
            },
            done: function() {
                this.ProgressStore.done();
            }
        }
    });
}

export const installProgress = {
    install(Vue, options) {
        let store = initStore(Vue);

        Vue.prototype.$progress = {}
        Vue.prototype.$progress.start = store.start;
        Vue.prototype.$progress.set = store.set;
        Vue.prototype.$progress.done = store.done;
        
        Vue.prototype.$ProgressStore = store.ProgressStore;
        Vue.component('Progress', ProgressVue);
    }
};