const Home = () => import("@/views/home/Home.vue");
const Tags = () => import("@/views/tags/Tags.vue");
const Related = () => import("@/views/related/Related.vue");
const Page404 = () => import("@/views/page404/Page404.vue");
const Archive = () => import("@/views/archive/Archive.vue");
const Archives = () => import("@/views/archives/Archives.vue");
const Categories = () => import("@/views/categories/Categories.vue");

export default [
    { path: "/", name: "home", component: Home },
    { path: '/tags', name: 'tags', component: Tags },
    { path: '/404', name: '404', component: Page404 },
    { path: '/archives', name: 'archives', component: Archives },
    { path: '/categories', name: 'categories', component: Categories },
    { path: '/related/:type/:slug', name: 'related', component: Related },
    { path: '/pages/*', name: 'page', component: Archive, props: { isImplicit: true } },
    { path: '/posts/:slug', name: 'post', component: Archive, props: { isImplicit: false } },
    { path: '/*', redirect: { name: '404' } }
];
