<template>
  <div id="app">
        <Progress />

        <Header :headerTop="headerTop" :headerHidden="headerHidden" />

        <main>
            <Background :scrolled="scrolled" :bg="bg" :bgHeight="bgHeight" />
            
            <router-view :scrolled="scrolled" :bgHeight="bgHeight" />
        </main>

        <Footer />
    </div>
</template>

<style lang="less">

</style>

<script>
/* eslint-disable */
import Header from "@/components/header/Header.vue";
import Footer from "@/components/footer/Footer.vue";
import Background from "@/components/background/Background.vue";

export default {
    name: "App",
    components: {
        Header,
        Footer,
        Background
    },
    data: () => ({
        scrolled: 0,
        headerTop: true,
        headerHidden: false,
        bgHeight: 512,
    }),
    mounted: function () {
        this.$progress.start();
        this.bgHeight = this.getBgHeight(window.innerHeight, '80', '60');
        this.initScroll();
        this.$progress.done();
    },
    computed: {
        theme: function() {
            return this.$store.state.global.themeConfig;
        },
        bg: function() {
            return this.$store.state.global.themeConfig.background.url;
        }
    },
    methods: {
        initScroll: function () {
            const h = this.bgHeight - 280;
            let that = this;
            
            window.addEventListener('scroll', () => {
                let scrolled = 0;
                if (typeof window.pageYOffset != "undefined") {
                    scrolled = window.pageYOffset;
                } else if (typeof document.compatMode != "undefined" && document.compatMode != "BackCompat") {
                    scrolled = document.documentElement.scrollTop;
                } else if (typeof document.body != "undefined") {
                    scrolled = document.body.scrollTop;
                }
                that.scrolled = scrolled * 0.3;

                if (scrolled >= h) {
                    that.headerTop = false;
                } else if (scrolled < h) {
                    that.headerTop = true;
                }

                window.addEventListener('scroll', () => {
                    let e = 0;
                    if (typeof window.pageYOffset != "undefined") {
                        e = window.pageYOffset;
                    } else if (typeof document.compatMode != "undefined" && document.compatMode != "BackCompat") {
                        e = document.documentElement.scrollTop;
                    } else if (typeof document.body != "undefined") {
                        e = document.body.scrollTop;
                    }

                    if (e < scrolled) {
                        that.headerHidden = false;
                    }
                    if (scrolled >= h && e > scrolled) {
                        that.headerHidden = true;
                    }
                })
            })
        },
        getBgHeight: (windowHeight, bannerHeight, mobileBannerHeight) => {
            windowHeight = windowHeight || 560;
            if (windowHeight > window.screen.availHeight) {
                windowHeight = window.screen.availHeight;
            }
            bannerHeight = bannerHeight.trim();
            mobileBannerHeight = mobileBannerHeight.trim();
            if (window.innerHeight > window.innerWidth) {
                bannerHeight = parseFloat(mobileBannerHeight);
            } else {
                bannerHeight = parseFloat(bannerHeight);
            }
            bannerHeight = Math.round(windowHeight * bannerHeight / 100);
            return bannerHeight;
        }
    }
};
</script>